/* Reset and Base Styles */
body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  background-color: #000; /* Fallback for video loading */
}

/* Main Wrapper - Full Screen Fixed */
.heroWrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  z-index: 0;
}

/* Hero Carousel Container */
.heroCarousel {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  user-select: none;
  -webkit-user-select: none;
}

/* Background Carousel - Full Bleed Video */
.bgCarousel {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.videoContainer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 1s cubic-bezier(0.65, 0, 0.35, 1);
}

.videoContainer.active {
  opacity: 1;
}

.video {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  object-fit: cover;
  pointer-events: none;
}

/* Foreground Carousel (Small Vertical) */
.fgCarousel {
  position: absolute;
  top: 55%;
  left: 10%;
  transform: translateY(-50%);
  width: 190px;
  height: 250px;
  z-index: 5;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.5);
}

.fgVideoContainer {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 1s cubic-bezier(0.65, 0, 0.35, 1);
}

.fgVideoContainer.active {
  opacity: 1;
}

.fgVideo {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Text Content */
.textContent {
  position: absolute;
  bottom: 15%;
  left: 10%;
  z-index: 10;
  color: white;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.5);
  max-width: 50%;
}

.title {
  font-size: 2.5rem;
  font-weight: 300;
  letter-spacing: 0.05em;
  margin-bottom: 2rem;
  line-height: 1.2;
  font-family: 'Helvetica Neue', sans-serif;
  text-transform: uppercase;
}

.cta {
  display: flex;
  gap: 2rem;
}

.caption {
  font-size: 0.75rem;
  text-transform: uppercase;
  letter-spacing: 0.2em;
  position: relative;
  cursor: pointer;
}

.caption::after {
  content: '';
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 0;
  height: 1px;
  background: white;
  transition: width 0.3s ease;
}

.caption:hover::after {
  width: 100%;
}

/* Navigation Controls */
.rightControls {
  position: absolute;
  right: 2rem;
  bottom: 20%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 2rem;
  z-index: 10;
}

.nav {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.prev, .next {
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  padding: 0.5rem;
  position: relative;
  overflow: hidden;
  transition: transform 0.3s ease;
}

.prev:hover, .next:hover {
  transform: scale(1.1);
}

.prev svg, .next svg {
  width: 0.5rem;
  height: 0.75rem;
  transition: all 0.3s ease;
}

.divider {
  width: 1px;
  height: 40px;
  background: rgba(255, 255, 255, 0.5);
}

.navHoverEffect {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%) scale(0);
  width: 1.5rem;
  height: 1.5rem;
  background: white;
  border-radius: 50%;
  opacity: 0;
  transition: all 0.3s ease;
  z-index: -1;
}

.prev:hover .navHoverEffect,
.next:hover .navHoverEffect {
  opacity: 0.2;
  transform: translateX(-50%) scale(1.5);
}

/* Next Up Section */
.nextUp {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.nextTitle {
  font-size: 1rem;
  margin: 0.5rem 0;
  font-weight: 300;
  letter-spacing: 0.05em;
}

.progressBar {
  height: 1px;
  width: 150px;
  background: white;
  transition: width 0.1s linear;
  position: relative;
}

.progressBar::after {
  content: '';
  position: absolute;
  top: -2px;
  left: 0;
  width: 100%;
  height: 5px;
  background: white;
  opacity: 0.2;
}

/* Responsive Adjustments */
@media (max-width: 768px) {
  .fgCarousel {
    width: 150px;
    height: 225px;
    left: 5%;
  }
  
  .textContent {
    left: 5%;
    bottom: 10%;
    max-width: 70%;
  }
  
  .title {
    font-size: 1.8rem;
  }
  
  .rightControls {
    right: 1rem;
    bottom: 15%;
  }
  
  .nextTitle {
    font-size: 0.875rem;
  }
  
  .progressBar {
    width: 100px;
  }
}

/* Orientation-specific adjustments */
@media (orientation: portrait) and (max-width: 768px) {
  .video {
    width: auto;
    height: 100%;
  }
  
  .fgCarousel {
    display: none; /* Hide on mobile portrait if needed */
  }
  
  .textContent {
    max-width: 90%;
  }
}

@media (orientation: landscape) and (max-height: 500px) {
  .textContent {
    bottom: 5%;
  }
  
  .rightControls {
    bottom: 10%;
  }
}
.pageWrapper {
  position: relative;
  overflow-x: hidden;
}

.heroWrapper {
  height: 100vh;
  width: 100%;
  position: relative;
  z-index: 1;
}

.firstSection {
  position: relative;
  z-index: 2;
  margin-top: -10vh; /* shows it poking into hero */
  transform: scale(0.5) translateY(50px);
  width: 60vw;
  height: 60vh;
  background: #111;
  color: white;
  border-radius: 20px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  will-change: transform;
  transition: all 0.3s ease;
}
